@import "./mixin.scss";

.full-width {
	width: 100%;
}

/* classes for blocks */
.block-container {
	display: flex;
	flex-flow: column;
	padding: 32px 18px;
	border-radius: 18pt;
	box-shadow: 1px 1px 6px var(--color-container-shadow);
	width: calc(100% - 32px);
	background-color: var(--color-white);
	@media only screen and (min-width: 992px) {
		padding: 32px 48px;
	}
}

.block-small {
	border-radius: 6px;
	padding: 8px 12px 8px;
}

.block-medium {
	padding: 20px 16px;
	margin: 4px 0px;
	border-radius: 6pt;
	border-width: 1px;
	border-style: solid;
	box-shadow: 0px 0px 6px var(--color-shadow);
	p {
		line-height: 23px;
		&:not(:last-child) {
			margin-bottom: 16px;
			@media only screen and (min-width: 600px) {
				margin-bottom: 6px;
			}
		}
	}
}

.block-large {
	padding: 32px;
	border-radius: 24px;
	box-shadow: 1px 1px 60px var(--color-container-shadow);
	p {
		letter-spacing: 0.14px;
		line-height: 21px;
	}
	@media only screen and (min-width: 768px) {
		padding: 32px 48px;
	}
}

.block-error {
	background-color: var(--color-error-bg);
	color: var(--color-error);
	border-color: var(--color-border-error);
}

.block-info {
	background-color: var(--color-info-bg);
	color: var(--color-info);
	border-color: var(--color-border-info);
}

.block-snippet {
	border-width: 1px;
	border-style: solid;
	padding: 3px 8px;
	border-radius: 4px;
}

.button {
	display: flex;
	align-items: center;
	padding: 8px 16px;
	font-size: 14px;
	line-height: 16px;
	background-color: var(--color-primary);
	border: 1px solid var(--color-primary-darker);
	font-weight: 600;
	color: var(--color-white);
	border-radius: 6px;
	font-family: inherit;
	cursor: pointer;
	@include gap-horizontal(0.75em);
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.15);
}

/* classes for font sizes */
.text-title {
	font-size: 24px;
	line-height: 40px;
	letter-spacing: 0.24px;
	font-weight: 600;
}

.text-small {
	font-size: 14px;
	line-height: 16px;
}

.text-bold {
	font-weight: 600;
}

/* classes for text colors */
.text-error {
	color: var(--color-error);
}

.text-info {
	color: var(--color-info);
}

.text-black {
	color: var(--color-black);
}

.text-label {
	color: var(--color-secondary-text);
}

/* classes for images */
.title-image {
	width: 35px;
	height: 32px;
	margin-bottom: 16px;
}

.title-image-smaller {
	width: 32px;
	height: 29px;
	margin-bottom: 16px;
}

/* classes for pages */
.with-footer {
	margin-bottom: 100px;
}
